export const markFormGroupTouched = (formGroup: FormGroup) => {
  Object.values(formGroup.controls).forEach(control => {
    control.markAsTouched();

    if (control instanceof FormGroup) {
      markFormGroupTouched(control);
    } else if (control instanceof FormArray) {
      (control as any).controls.forEach((c: any) => {
        if (c instanceof FormGroup) {
          markFormGroupTouched(c);
        } else if (c instanceof FormControl) {
          c.markAsTouched();
        }
      });
    }
  });
}

export const uploadFile = (request: string, file_name: string, IComMessageService: any, data?: any, keepFileName?: boolean, ignoreParseTime?: boolean) => {
  const xhr = new XMLHttpRequest();

  xhr.open('POST', request, true);
  xhr.responseType = 'blob';
  let token = localStorage?.getItem('token');
  while(token?.includes('"')){
    token = token?.replace('"', "");
 }
  xhr.setRequestHeader("Authorization", `Bearer ${token}`);
  xhr.onload = () => {
    if (xhr.status === 200) {
      const blob = new Blob([xhr.response], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
      const downloadUrl = URL.createObjectURL(blob);
      const a = document.createElement('a');
      const contentDisposition = xhr.getResponseHeader('Content-Disposition');
      let dateString = '';
      if(!keepFileName) {
        const currentDate = new Date();
        dateString = `${currentDate.getDate()}_${currentDate.getMonth() + 1}_${currentDate.getFullYear()}`;
      }
      let filename = `${file_name}_${dateString}`;
      if (contentDisposition) {
        const filenameRegex = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/;
        const matches = filenameRegex.exec(contentDisposition);
        if (matches != null && matches[1]) {
          filename = matches[1].replace(/['"]/g, '');
        }
      }

      a.href = downloadUrl;
      a.download = filename;  // Tên tệp xuất
      document.body.appendChild(a);
      a.click();

      URL.revokeObjectURL(downloadUrl);
      document.body.removeChild(a);
      IComMessageService.showAlertMessageDone('Tải file thành công.');
    } else {
      IComMessageService.showAlertMessageDone('Tải file thất bại.');
    }
  };

  xhr.onerror = function () {
    console.error('Request error');
  };

  xhr.setRequestHeader('Content-Type', 'application/json');
  data && ignoreParseTime && parseDateRangeDayMonthYearToTwoKey(data);
  xhr.send(JSON.stringify(data));
}